    transition-timing-function: cubic-bezier(.4, 0, .2, 1);
    transition-duration: .15s
  }

  .duration-200 {
    transition-duration: .2s
  }

  .ease-out {
    transition-timing-function: cubic-bezier(0, 0, .2, 1)
  }

  body {
    height: 100vh;
    height: 100dvh;
    font-family: Helvetica Neue, sans-serif;
    -webkit-font-smoothing: subpixel-antialiased;
    -webkit-text-size-adjust: 100%
  }

  h2 {
    margin-bottom: .5rem;
    font-size: 1.25rem;
    line-height: 1.75rem
  }

  h2,
  h3 {
    margin-bottom: .5rem;
    width: 100%;
    font-family: AvantGarde, sans-serif;
    font-size: 1rem;
    line-height: 1.5rem
  }

  table {
    width: 100%;
    border-collapse: collapse;
    text-align: left;
    font-size: .875rem;
    line-height: 1.25rem
  }

  table .alternate th,
  table .alternate td {
    background: var(--color-result-row-alternate)
  }

  table thead th,
  table thead td {
    border-bottom: 1px solid var(--color-border);
    font-family: AvantGarde, sans-serif
  }

  table tbody th {
    max-width: 30vw;
    font-weight: 400
  }

  table th,
  table td {
    padding: 1.25rem 2.5rem
  }

  @media not all and (min-width: 768px) {

    table th,
    table td {
      padding-left: 1rem;
      padding-right: 1rem
    }
  }

  table th,
  table td {
    border-top: 1px solid var(--color-border)
  }

  table th.indent,
  table td.indent {
    padding-left: 74px
  }

  @media not all and (min-width: 768px) {

    table th.indent,
    table td.indent {
      padding-left: 40px
    }
  }

  table tfoot th {
    background: var(--color-result-row-foot)
  }

  table thead th {
    background: var(--color-result-row-head)
  }

  select {
    text-align: left;
    -moz-appearance: none;
    -webkit-appearance: none;
    appearance: none;
    background-image: url("data:image/svg+xml;utf8,<svg width='16' height='8' viewBox='0 0 16 8' fill='none' xmlns='http://www.w3.org/2000/svg'><path d='M7.79297 7.75011L0.00390625 0H15.582L7.79297 7.75011Z' fill='grey'/></svg>");
    background-repeat: no-repeat;
    background-position-y: 15px;
    background-position-x: calc(100% - 16px)
  }

  @media not all and (min-width: 768px) {
    .max-md\:col-span-1 {
      grid-column: span 1 / span 1
    }

    .max-md\:w-full {
      width: 100%
    }

    .max-md\:grid-cols-1 {
      grid-template-columns: repeat(1, minmax(0, 1fr))
    }

    .max-md\:flex-col {
      flex-direction: column
    }

    .max-md\:px-4 {
      padding-left: 1rem;
      padding-right: 1rem
    }

    .max-md\:py-4 {
      padding-top: 1rem;
      padding-bottom: 1rem
    }

    .max-md\:pb-2 {
      padding-bottom: .5rem
    }

    .max-md\:pt-4 {
      padding-top: 1rem
    }

    .max-md\:text-sm {
      font-size: .875rem;
      line-height: 1.25rem
    }
  }

  @media (min-width: 768px) {
    .md\:w-\[40\%\] {
      width: 40%
    }

    .md\:justify-start {
      justify-content: flex-start
    }
  }

  @media (min-width: 1024px) {
    .lg\:p-8 {
      padding: 2rem
    }
  }

  .\[\&_p\+p\]\:mt-3 p+p {
    margin-top: .75rem
  }

  .\[\&_td\]\:text-right td {
    text-align: right
  }
